<template>
  <div>
    <h1 class="text-center"> </h1>
    <table class="table table-bordered">
      <thead>
        <th> </th>
        <th></th>
      </thead>
      <tbody>
        <tr v-for="(item, index) in users" :key="index">
          <td>{{ item.user_name }}</td>
          <td v-if="+item.is_admin"></td>
          <td v-else></td>
        </tr>
      </tbody>
    </table>
    {{ users }}
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      users: []
    }
  },
  created() {
    axios.get('/backend/users.php')
      .then(res => {
        this.users = res.data;
      })
  }
};
</script>



